<template>
    <div class="task" :class="{ 'deleted': isDeleted }" v-if="objectTask != null">
        <p class="id-task" v-show="false">{{ objectTask.id }}</p>
        <div class="priority-task"></div>
        <div class="box-text">
            <span class="fix-box title-task">{{ objectTask.title }} </span>
            <span class="fix-box description-task" v-show="false"></span>
        </div>
        <label class="tik-task">
            <input type="checkbox" v-model="objectTask.is_complated" :disabled="objectTask.is_complated">
            <span class="checkmark"></span>
        </label>

    </div>
</template>

<script setup>

import { watch, ref, reactive } from "vue";

import { DB, info } from "../rizi_code_js/process_task";

const props = defineProps(["taskInfo"]);
const objectTask = reactive(props.taskInfo);

const isDeleted = ref(false);


watch(objectTask, () => {
    DB.set_db_stroge(info.name_task_Complated, objectTask);
    DB.delete_stroge(info.name_task_notComplated, objectTask.id);
    console.log(objectTask.is_complated);

    isDeleted.value = objectTask.is_complated;
})
</script>

<style scoped>
.task {
    animation: both anime_start_task_bord 0.2s;
    display: flex;
    flex-direction: row;

    align-items: center;

    width: 100%;
    height: 75px;

    border-bottom: solid 1px #0005;
    background: var(--bord-color);
}

@keyframes anime_start_task_bord {
    0% {
        height: 0;
        opacity: 0;
    }

    100% {
        height: 75px;
        opacity: 1;
    }
}

.deleted {
    display: flex;
    flex-direction: row;

    align-items: center;

    width: 100%;
    height: 75px;

    animation: both anime_deleted_task_bord 2s;

    border-bottom: solid 1px #0005;
    background: var(--bord-color);
}

@keyframes anime_deleted_task_bord {
    0% {
        display: flex;
        opacity: 1;
    }
    20%{
        height: 75px;
        opacity: 0.5;
    }
    90% {
        background-color: #3332;
        height: 0;
    }

    100% {
        height: 0;

        display: none;
        opacity: 0;
    }
}


.task .priority-task {
    height: 100%;
    width: 15px;

    border-left: solid 1px #fff;
    background-color: #88f;
}

.task .box-text {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;

    max-width: 100%;
    min-width: 120px;
    width: max-content;
    height: 100%;

    margin-left: 60px;
}

.fix-box {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;

    width: 100%;

    overflow: hidden;
    text-wrap: nowrap !important;
    text-shadow: 0 0 0.1px #0005;

}


.task .fix-box::after {
    content: '';
    width: 10%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;

    background: linear-gradient(-90deg, transparent 0, var(--bord-color) 100%);
}


.task .title-task {
    height: 70%;

    padding: 4px 8px 0;

    font: bold 24px "b yekan";

    color: #eee;
}

.task .description-task {
    height: 30%;

    padding: 0 8px 8px 0;

    font: bold 16px "b yekan";

    color: #aaa;
}


.task .tik-task {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 60px;

}



.task .tik-task input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;

    overflow: hidden;
    border-radius: 14px;
    border: solid 1px #ccc;
    box-shadow: inset 0 0 15px #333;


}

.task .tik-task:hover input~.checkmark {
    box-shadow: inset 0 0 8px #333;
}

.task .tik-task input:checked~.checkmark {
    box-shadow: inset 0 0 5px #222;
}

.checkmark:after {
    content: "";
    display: none;
}


.task .tik-task input:checked~.checkmark:after {
    display: flex;
    box-shadow: 2px 2px 1px #222;
}


.task .tik-task .checkmark:after {
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 6px;
    height: 15px;
    border-radius: 2px;
    border: solid #ccc;

    border-width: 0 5px 5px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}


/*before*/
.checkmark:before {
    content: "";
    display: flex;
    flex-direction: column;

    background-color: #fff;
}


.task .tik-task input:checked~.checkmark:before {
    position: absolute;

    width: 120%;
    height: 120%;

    border-radius: 0;

    transition: 0.3s;

    background-color: #333;
}


.task .tik-task .checkmark:before {
    width: 0;
    height: 0;

    /* background-color: green; */

    border-radius: 50px;

}
</style>