<template>
    <div class="container">
        <div class="roller">

            <rizi_task :task-info="task" v-for="(task, i) in tasksNotComplated" :key="i" />

            <div class="nothing" v-if="!tasksNotComplated.length">
                <p>هیچ فعالیت ثبت نشده است !</p>
            </div>
        </div>
    </div>
</template>

<script setup>

import { watch, ref } from "vue";
import { DB, info, counter_db_stroge } from "../rizi_code_js/process_task";

//Components
import rizi_task from "./rizi_task.vue";
import { onBeforeRouteUpdate } from "vue-router";

const tasksNotComplated = ref(DB.get_db_stroge(info.name_task_notComplated));

onBeforeRouteUpdate(() => {
    tasksNotComplated.value = DB.get_db_stroge(info.name_task_notComplated);

})
watch(counter_db_stroge, () => {
    tasksNotComplated.value = DB.get_db_stroge(info.name_task_notComplated);
    setTimeout(() => {
        DB.fix_stroge(info.name_task_notComplated);

    }, 100);
})

</script>

<style scoped>
.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    overflow-y: auto;
    overflow-x: hidden;

    height: -webkit-fill-available;
    height: inherit;
    width: 100%;

    margin: 2px 0;
    background: var(--back-color);
}

.container .roller {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: auto;

    height: fit-content;
    width: 100%;
}


.nothing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60px;
}

.nothing p {
    color: white;
    font: bold 24px "b yekan";
    text-shadow: 0 0 2px #2228;
}
</style>