import { ref } from "vue";

export const counter_db_stroge = ref(0);

export let info = {
    end_id: (name_db) => {
        let res = DB.get_db_stroge(name_db).length
        return res;
    },
    //not complated task version 1
    name_task_notComplated: "nCtv1",
    name_task_Complated: "Ctv1",
}

export const OBJ_TASK = {

    create: (id, title, priority, status_t, start_date, end_date, is_complated) => {
        let new_obj = { id, title, priority, status: "", start_date, end_date, is_complated };
        new_obj.id = id;
        new_obj.title = title;
        new_obj.priority = priority;
        new_obj.status = status_t;
        new_obj.start_date = start_date;
        new_obj.end_date = end_date;
        new_obj.is_complated = is_complated;
        return new_obj;
    }
}

//create Class Arr for save array in ver 2

export const DB = {
    fix_stroge: (name_db) => {
        let is_not_null = false;
        let new_db = DB.get_db_stroge(name_db);
        new_db.forEach(o => {
            if (o != null) {
                is_not_null = true;
            }
        });
        if (!is_not_null) {
            localStorage.removeItem(name_db);
            counter_db_stroge.value = 0;
        }
    },
    search_stroge: (name_db, obj) => {
        let res = false;
        let new_db = DB.get_db_stroge(name_db);
        new_db.forEach(o => {
            if (o != null && o.title == obj.title) {
                res = true;
            }
        });
        return res;
    },
    update_stroge: (name_db, id, newObj) => {
        let new_db = DB.get_db_stroge(name_db);
        new_db[id] = newObj;
        counter_db_stroge.value++;
        localStorage.setItem(name_db, JSON.stringify(new_db));
    },
    delete_stroge: (name_db, id) => {
        let new_db = DB.get_db_stroge(name_db);
        new_db[id] = null;
        counter_db_stroge.value = new_db.forEach(val => {
            let i=0;
            if(val != null){
                i++;
            }
            return i;
        });;
        localStorage.setItem(name_db, JSON.stringify(new_db));
    },
    get_db_stroge: (name_db) => {
        let res = localStorage.getItem(name_db);
        if (res == null || res == "") {
            localStorage.setItem(name_db, JSON.stringify([]));
        }
        res = JSON.parse(localStorage.getItem(name_db));
        counter_db_stroge.value = res.length;
        return res;
    },
    set_db_stroge: (name_db, obj) => {
        let new_db = DB.get_db_stroge(name_db);
        new_db[new_db.length] = obj;
        counter_db_stroge.value = new_db.length;
        localStorage.setItem(name_db, JSON.stringify(new_db));
    }
   
}

