<template>
    <div class="box">

        <div :class="{ 'send-box': len, 'send-box-off': is_run && len == 0 }" v-show="is_run" @click="send_text()">
            <img src="../assets/icons/send-icon.png" alt="send">
        </div>

        <div class="inp-box">
            <input dir="rtl" class="inp empty:flex" contenteditable="true" :maxlength="max_len" v-model="inp_title"
                @keydown="send_text($event)" @input="input_text()" placeholder="متن خود را وارد کنید..." />

            <span class="len-inp">{{ max_len }} / {{ len }}</span>

        </div>

    </div>
</template>

<script setup>
import { ref } from 'vue';
import { info, DB, OBJ_TASK } from "../rizi_code_js/process_task";

const inp_title = ref('');
const max_len = 50;
const len = ref(0);
const is_run = ref(false);

const emitSelf = defineEmits(['inp_val'])

function input_text() {
    if (inp_title.value != null) {
        len.value = inp_title.value.length;
    }
    is_run.value = true;
}

function send_text(e) {
    if (e != null) {
        if (e.type == "keydown" && (e.code != "Enter" && e.code != "NumpadEnter")) {
            return;
        }
    }
    if (inp_title.value.length <= 0) return;

    const obj = OBJ_TASK.create(info.end_id(info.name_task_notComplated), inp_title.value, 0, "notComplated", Date(), Date(), false);
    if (!DB.search_stroge(info.name_task_notComplated, obj)) {
        DB.set_db_stroge(info.name_task_notComplated, obj);
    }
    inp_title.value = "";
    input_text();
}

</script>

<style scoped>
.box {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    width: 100%;
    min-height: 60px;

    background: var(--input-color);
}

.box .inp-box {
    display: flex;
    flex-direction: row;
    align-items: center;

    height: 100%;
    width: 100%;


}

.box .inp-box .inp {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;

    padding: 5px 20px;
    max-height: 300px;
    word-break: break-word;
    white-space: pre-wrap;

    outline: none;
    border: none;

    color: #ddd;
    background: var(--input-color);
    font: bold 25px "b yekan";

}

.box .inp-box .inp:empty:after {
    content: attr(placeholder);

    color: #ccc9;
}

.inp-box .len-inp {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;

    width: 80px;

    overflow: hidden;

    font: bold 18px "b yekan";

    text-shadow: 0 0 1px #0009;
    color: #ccc9;
}

.box .send-box-off {
    animation: both send-anime-off 0.3s;
}

@keyframes send-anime-off {
    0% {
        width: 50px;
        height: 50px;
        /* opacity: 1; */
    }

    70% {
        width: 0;
        height: 50px;
        /* opacity: 0; */
    }

    100% {
        width: 0;
        height: 0;
        /* opacity: 0; */
        display: none;
    }
}

.box .send-box {
    animation: both send-anime 0.3s;

}

@keyframes send-anime {
    0% {

        width: 0;
        height: 0;
        /* opacity: 0; */
        display: none;
    }

    10% {
        width: 0;
        height: 50px;
        /* opacity: 0; */
    }

    100% {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        width: 80px;
        height: 50px;
        /* opacity: 1; */
    }
}


.box .send-box img,
.box .send-box-off img {
    height: 100%;
}
</style>